<template>
    <div>
      <div ref="barChartDom" style="width: 600px; height: 400px;"></div>
      <div ref="pieChartDom" style="width: 600px; height: 400px;"></div>
      <div ref="lineChartDom" style="width: 600px; height: 400px;"></div>
    </div>
  </template>
  
  <script setup>
  import { ref, onMounted } from 'vue';
  import * as echarts from 'echarts';
  
  // 创建响应式引用来保存DOM元素
  const barChartDom = ref(null);
  const pieChartDom = ref(null);
  const lineChartDom = ref(null);
  let barChartInstance = null;
  let pieChartInstance = null;
  let lineChartInstance = null;
  
  onMounted(() => {
    // 初始化柱状图
    barChartInstance = echarts.init(barChartDom.value);
    const barOption = {
      title: {
        text: '柱状图示例'
      },
      tooltip: {},
      legend: {
        data:['销量']
      },
      xAxis: {
        data: ["衬衫","羊毛衫","雪纺衫","裤子","高跟鞋","袜子"]
      },
      yAxis: {},
      series: [{
        name: '销量',
        type: 'bar',
        data: [5, 20, 36, 10, 10, 20]
      }]
    };
    barChartInstance.setOption(barOption);
  
    // 初始化饼图
    pieChartInstance = echarts.init(pieChartDom.value);
    const pieOption = {
      title: {
        text: '饼图示例'
      },
      tooltip: {
        trigger: 'item'
      },
      legend: {
        orient: 'vertical',
        left: 'left',
      },
      series: [
        {
          name: '访问来源',
          type: 'pie',
          radius: '50%',
          data: [
            { value: 1048, name: '搜索引擎' },
            { value: 735, name: '直接访问' },
            { value: 580, name: '邮件营销' },
            { value: 484, name: '联盟广告' },
            { value: 300, name: '视频广告' }
          ],
          emphasis: {
            itemStyle: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            }
          }
        }
      ]
    };
    pieChartInstance.setOption(pieOption);
  
    // 初始化折线图
    lineChartInstance = echarts.init(lineChartDom.value);
    const lineOption = {
      title: {
        text: '折线图示例'
      },
      tooltip: {
        trigger: 'axis'
      },
      legend: {
        data: ['销量']
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      toolbox: {
        feature: {
          saveAsImage: {}
        }
      },
      xAxis: {
        type: 'category',
        boundaryGap: false,
        data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
      },
      yAxis: {
        type: 'value'
      },
      series: [
        {
          name: '销量',
          type: 'line',
          stack: '总量',
          data: [120, 132, 101, 134, 90, 230, 210]
        }
      ]
    };
    lineChartInstance.setOption(lineOption);
  });
  </script>